@import "../../scss/variables";

.card-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    gap: 2rem;

    .cards{
        width: 500px;
        height: 350px; 
        position: relative;
        overflow: hidden;

        &:hover .card-over{
            transform: translateY(0%);
        }

        &:hover img{
            opacity: 1;

        }

    }

    .title{
        position: absolute;
        top: 0%;
        left: 0%;
        padding: 1rem;
        border-radius: 10px 20px 20px 20px ;
        background-color: rgba(0, 0, 0, 0.8);
        color: $cardNavBgColor;
        z-index: 2;

    }
    .date{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        padding: 1rem;
        border-radius: 20px ;
        background-color: rgba(0, 0, 0, 0.8);
        color: $cardNavBgColor;
        z-index: 2;
        font-size: 0.3rem;
        // font-family:  'Almendra Display', cursive;

    }

    img{
        width: 500px;
        height: 350px;
        object-fit: cover;
        opacity: 0.5;
        border-radius: 10px;

    }

    .card-over{
        position: absolute;
        bottom: 0rem;
        left: 0;
        background-color: rgba(0, 0, 0, 0.8);
        z-index: 3;
        font-size: 1.5rem;
        padding: 1rem;
        border-radius: 10px;
        transform: translateY(100%);
        transition: transform 0.7s ease-in-out;
        max-height: 75%;
        overflow: auto;
        &::-webkit-scrollbar {
            display: none;
        }

    }
}





